.navbar {
        position: sticky;
        top: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #66768F;
        color: #C5D5E4;
        padding: 1rem;
      }
      
      .navbar-left .logo {
        font-size: 1.5rem;
        font-weight: bold;
        color: #C5D5E4;
        text-decoration: none;
      }
      
      .navbar-center .nav-links {
        list-style-type: none;
        display: flex;
        flex-direction: row;
        gap: 10px;
      }
      
      .navbar-center .nav-links a {
        color: #C5D5E4;
        text-decoration: none;
      }
      
